body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#map {
    height: 100%;
}

.stepsContainer {
    position: fixed;
    /* top: 0; */
    left: 0;
}


.EditorContainer {
    overflow-y: scroll;
    font-size: small;
    max-height: 100vh;
}

.EditorCard {
    padding: 10px;
}

.EditorContent {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}


.form-group {
    display: grid;
    grid-template-columns: auto 8rem;
    grid-column-gap: 0.5rem;
    padding-bottom: 0.5rem;
}

.form-group label {
    padding-top: 0.5rem;
}

form-input {
    margin-bottom: 0.5rem;
}

.drawkitToolList {
    box-shadow: 2px 2px 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    background-color: #0074d9;
    border-radius: 4px;
    border: 2px solid rgba(0,0,0,.2);
    margin-bottom: 1rem;
}

.drawkitTool {
    color: inherit;
    width: auto;
    height: auto;
    text-align: center;
    cursor: pointer;
}

.drawkitTool.selected {
    font-weight: bold;
    font-size: larger;
}

.drawkitToolList > span > a {
    display: block;
    width: 100%;
    height: 100%;
    line-height: 35px;
    color: inherit;
    text-decoration: none;
    text-align: center;
}

.drawkitClearAll {
    margin-bottom: 1rem;
}
.App {
  display: grid;
  grid-template-columns: 1fr;
  height: 100vh;
}

MapView {
  grid-column-start: 1;
}

SideView {
  grid-column-start: 2;
}

.initialModalBody {
  text-align: center;
}

.initialModalTitle {
  text-align: center;
}

